.player{

    display: flex;
    min-height: 20vh;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

}
.time-control{
    width: 50%;
    display: flex;
    input {
        width: 100%;
        padding: 1rem 0rem;
    -webkit-appearance: none;
    background: transparent;
    cursor: pointer;
}
    
    p{
        padding: 1rem;
    }
}
.player-control{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 30%;
    svg{
        cursor: pointer;

    }
}

@media screen and (max-width: 768px){
    .time-control{
        width: 90%;
    }
    .player-control{
        width: 60%;
    }
}